<template>
  <div>
    <h2>Dashboard</h2>

    <app-panel-group
      :daily-revenue="panelGroup.dailyRevenue"
      :monthly-revenue="panelGroup.monthlyRevenue"
      :top-product="panelGroup.topSellingProduct"
      :top-shop="panelGroup.topSeelingShop"
    ></app-panel-group>

    <el-row :gutter="8">
      <el-col :xs="{span: 24}" :sm="{span: 24}" :md="{span: 24}" :lg="{span: 18}" :xl="{span: 18}" style="padding-right:8px;margin-bottom:30px;">
        <el-row style="background:#fff;padding:16px 16px 0;margin-bottom:32px;">
          <h3>Revenue Statistic</h3>
          <app-revenue-statistic-chart
            :chart-data="revenueStatistic.chartData"
            v-if="revenueStatistic.chartData.xAxisData"
          ></app-revenue-statistic-chart>
        </el-row>
      </el-col>
      <!-- <el-col :xs="{span: 12}" :sm="{span: 12}" :md="{span: 12}" :lg="{span: 6}" :xl="{span: 5}"> -->
      <el-col :xs="{span: 24}" :sm="{span: 24}" :md="{span: 24}" :lg="{span: 6}" :xl="{span: 6}">
        <app-product-need-stocked
          :products="productNeedToStocked"
          v-if="productNeedToStocked.length > 0"
        ></app-product-need-stocked>
      </el-col>
      <!-- <el-col :xs="{span: 12}" :sm="{span: 12}" :md="{span: 12}" :lg="{span: 6}" :xl="{span: 5}">
      </el-col> -->
    </el-row>

  </div>
</template>

<script>
import moment from "moment";
import PanelGroup from "@/components/Dashboard/PanelGroup";
import RevenueStatisticChart from "@/components/Dashboard/RevenueStatisticChart";
import ProductNeedStocked from "@/components/Dashboard/ProductNeedStocked";

export default {
  components: {
    appPanelGroup: PanelGroup,
    appRevenueStatisticChart: RevenueStatisticChart,
    appProductNeedStocked: ProductNeedStocked
  },
  data() {
    return {
      panelGroup: {
        monthlyRevenue: 0,
        dailyRevenue: 0,
        topSeelingShop: null,
        topSellingProduct: null
      },
      revenueStatistic: {
        chartData: {}
      },
      productNeedToStocked: []
    };
  },
  methods: {
    getDashboard() {
      this.loading = true;
      this.$store
        .dispatch("dashboard/FetchDashboard", this.urlParams)
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;

          console.log("FETCH DASHBOARD: ", data);

          // Panel Group
          this.panelGroup = {
            monthlyRevenue: data.data.monthly_revenue,
            dailyRevenue: data.data.daily_revenue,
            topSellingProduct: data.data.top_selling_product,
            topSeelingShop: data.data.top_selling_shop
          };

          // Product Need Stocked
          this.productNeedToStocked = data.data.products_need_stocked;

          // Revenue Statistic
          const revenueStatistic =
            data.data.revenue_statistic.revenue_statistic;
          const profitStatistic = data.data.revenue_statistic.profit_statistic;
          this.revenueStatistic = {
            chartData: {
              profitData: profitStatistic.map(r => {
                return r.profit;
              }),
              revenueData: revenueStatistic.map(r => {
                return r.revenue;
              }),
              xAxisData: revenueStatistic.map(r => {
                return moment(r.date).format("DD MMM");
              })
            }
          };
          this.loading = false;
        });
    }
  },
  created() {
    this.getDashboard();
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>




