<template>
  <div>
    <h3><icon name="exclamation-triangle" style="margin-right:5px;color:red;"></icon>Stock Habis</h3>

    <el-table :data="products" v-loading="loading" element-loading-text="Memuat data..." border fit highlight-current-row style="width: 100%">
    <el-table-column prop="name" label="Product Name" sortable align="left" header-align="left">
      <template slot-scope="scope">
        <div>{{ scope.row.name }}</div>
      </template>
    </el-table-column>

    <el-table-column prop="payment" label="Shops" sortable align="lefts" header-align="left">
      <template slot-scope="scope">
        <el-tag
          style="margin:5px"
          v-if="scope.row.shops"
          v-for="shop in scope.row.shops"
          :key="shop.shop_id"
          type="info">{{shop.shop_name}} ({{shop.stock}})</el-tag>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
export default {
  props: {
    products: {
      type: Array,
      required: true
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    _products() {
      if (this.products) {
        return this.products;
      } else {
        return [];
      }
    }
  }
};
</script>



